import axios from 'axios'

const baseUrl = '/api/blogs'

let token = null

const setToken = (key) => {
    token = `bearer ${key}`
}

const loadBlogs = async () => {
  const config = {
    headers: { Authorization: token }
  }
  const response = await axios.get(baseUrl, config)
  return response.data
}

const createBlog = async (blogInfo) => {
  const config = {
    headers: { Authorization: token }
  }
  const response = await axios.post(baseUrl, blogInfo, config)
  return response.data
}

const update = async (blogInfo, id) => {
  const config = {
    headers: { Authorization: token}
  }
  const response = await axios.put(`${baseUrl}/${id}`, blogInfo, config)
  return response.data
}

const like = async (blogInfo, id) => {
  const config = {
    headers: { Authorization: token }
  }
  const resposne = await axios.put(`${baseUrl}/like/${id}`, blogInfo, config)
  return resposne.data
}

const del = async (id) => {
  const config = {
    headers: { Authorization: token }
  }
  await axios.delete(`${baseUrl}/${id}`, config)

}

const exp = {
    setToken,
    loadBlogs,
    createBlog,
    update,
    like,
    del
}

export default exp