import React, { useState } from 'react'
import { TextField, Button } from "@mui/material";
import loginServices from '../services/login'
import blogServices from '../services/blogs'

function LoginForm({ setUser, setNewUser }) {
  const [ username, setUsername ] = useState('')
  const [ password, setPassword ] = useState('')
  
  const handleLogin = async (e) => {
    e.preventDefault()
    try {
        const returnedUser = await loginServices.login({ username, password })
        window.localStorage.setItem('localUser', JSON.stringify(returnedUser))
        blogServices.setToken(returnedUser.token)
        setUser(returnedUser)
        setUsername('')
        setPassword('')
    } catch (exception) {
        console.log(`exception: ${exception}`)
    }
  }

  return (
    <>
      <h1 style={{ color: 'rgb(255, 165, 0)' }}>Blog Saver</h1>
      <div className="body">
        <div className="column">
            <form onSubmit={handleLogin} className="form">            
                <TextField
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                name="Username"
                placeholder="username"
                InputProps={{
                    className: "input",
                }}
                />
                <TextField
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                name="Password"
                placeholder="password"
                InputProps={{
                    className: "input",
                }}
                />
                <Button type="submit">login</Button>
            </form> 
            <Button onClick={() => setNewUser(true)}>Create Account</Button>      
        </div>
      </div>
    </>
  )
}

export default LoginForm